<template>
  <div class="row">
    <modal :show="modalStatus">
      <div class="d-flex align-items-center gap-4">
        <h6 class="modal-title" id="modal-title-default">Edit Admin Info</h6>
      </div>

      <p class="mt-4">change these data in order to update <strong>Admin</strong> information</p>

      <form @submit.prevent="">
        <div class="d-flex flex-column">
          <input-component
            @input="name"
            type="text"
            placeholder="name"
            name="name"
            size="md"
            :value="form.name ?? collectionDetails.name"
            :valid="formValidation.name.isValid"
            :startValidate="formValidation.name.startValidate"
            :errorMessage="formValidation.name.errorMessage"
          />
        </div>

        <div class="d-flex justify-content-between gap-1">
          <button-component
            class="mt-4"
            variant="gradient"
            color="success"
            size="sm"
            :isLoading="actionInProgress.loading"
            :disabled="actionInProgress.loading || formStatus"
            @click="editOrganizer()"
          >
            Submit
          </button-component>
          <button-component class="mt-4" variant="gradient" color="danger" size="sm" @click="toggleModal()">
            Close
          </button-component>
        </div>
      </form>
    </modal>
  </div>
</template>

<script>
  import Modal from '@/components/Modal-dialog';
  import ButtonComponent from '@/components/Button.vue';
  import InputComponent from '@/components/Input.vue';

  export default {
    name: 'edit-organizer',
    components: { Modal, ButtonComponent, InputComponent },
    data() {
      return {
        form: {
          id: null,
          name: null,
        },
        formValidation: {
          name: { isValid: false, startValidate: false, errorMessage: '' },
        },
      };
    },
    computed: {
      modalStatus() {
        return this.$store.state.CollectionStore.modals.editCollection;
      },
      actionInProgress() {
        return this.$store.state.CollectionStore.progress.updateCollections;
      },
      collectionDetails() {
        return this.$store.state.CollectionStore.editCollectionDetails;
      },
      formStatus() {
        return this.formValidation.name.isValid;
      },
    },

    methods: {
      name(event) {
        this.form.name = event.target.value;
      },

      toggleModal() {
        this.$store.commit('CollectionStore/TOGGLE_EDIT_COLLECTION_MODAL');
      },

      startValidation() {
        if (!this.form.name) {
          this.form.name = this.collectionDetails.name;
        }
        this.form.id = this.collectionDetails.id;
        this.formValidation.name.startValidate = true;
      },

      checkForm() {
        this.startValidation();
        if (!this.form.name.length) {
          this.formValidation.name.isValid = false;
          this.formValidation.name.errorMessage = 'Name Field is required';
        } else {
          this.formValidation.name.isValid = true;
        }
      },
      editOrganizer() {
        this.checkForm();
        if (this.formStatus) {
          this.$store.dispatch('CollectionStore/updateCollection', this.form);
        }
      },
    },
  };
</script>
